.student-list-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #f0f0f0;
  padding: 20px;
  padding-top: 100px;
}

.student-list-container h1 {
  margin-bottom: 20px;
  color: #333;
}

.student-list-container table {
  width: 80%;
  border-collapse: collapse;
  margin: 20px 0;
}

.student-list-container table,
.student-list-container th,
.student-list-container td {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: left;
}

.student-list-container th {
  background-color: #007bff;
  color: white;
}

.student-list-container td {
  background-color: white;
}

.student-list-container nav {
  margin-top: 20px;
}

.student-list-container nav a {
  color: #007bff;
  text-decoration: none;
}

.student-list-container nav a:hover {
  text-decoration: underline;
}

.student-list-container button {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 3px;
}

.student-list-container button:hover {
  background-color: #c82333;
}

/* Media Query for Tablets */
@media (max-width: 768px) {
  .student-list-container table {
    width: 90%;
  }

  .student-list-container table,
  .student-list-container th,
  .student-list-container td {
    font-size: 16px;
  }

  .student-list-container button {
    padding: 7px 15px;
    font-size: 16px;
  }
}

/* Media Query for Mobile Devices */
@media (max-width: 480px) {
  .student-list-container table {
    width: 100%;
    font-size: 14px;
  }

  .student-list-container button {
    padding: 8px 20px;
    font-size: 14px;
  }
}
